
import React from 'react'
import useSWR from "swr";

const fetcher = async (...args) => {
    const res = await fetch(...args);
    const data = await res.json();
    return data;
}
export default function CarList() {

    const { data } = useSWR("https://noun.achievextesting.one/api/v1/admin/cars", fetcher, {
        suspense: true
    });
    console.log(`hello july`)
    console.log(data['data'])
    let allCars = data['data'];
    return (
        <>
            {
                allCars.map(function (item, i) {
                    console.log(item);
                    return <tr key={i}>
                        <td>{i + 1}</td>
                        <td><img width={80} src={item.carImage} alt="" /></td>
                        <td>{item.portName}</td>
                        <td>{item.carBrand}</td>
                        <td>{item.carModel}</td>
                        <td>{item.carSubModel}</td>
                    </tr>
                })
            }
        </>
    )
}
