import axios from 'axios';
import React, { Suspense } from 'react'
import { useState } from 'react';
import { useEffect } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import CarList from '../components/CarList';
import TableSkeleton from '../components/TableSkeleton';

const Cars = () => {
    let history = useHistory();
    const [car, setCar] = useState([]);

    useEffect(() => {
        const item = localStorage.getItem("adminDetails");
        if (item) {
        } else {
            history.push("/Login");
        }
    }, [])

    return (
        <div>
            <h2 className="page-header">
                Cars
                <Link to={"/add-car"} className='btn btn-success'> +Add</Link>
            </h2>
            <div className='row'>
                <table>
                    <tr>
                        <th>SL.No.</th>
                        <th>Image</th>
                        <th>Charging Port</th>
                        <th>Brand Name</th>
                        <th>Model Name</th>
                        <th>Sub Model</th>
                    </tr>
                    <Suspense fallback={<TableSkeleton counts={15} />}>
                        <CarList />
                    </Suspense>
                </table>
            </div>
        </div>
    )
}

export default Cars