import React from 'react'

import { Route, Switch } from 'react-router-dom'

import Dashboard from '../pages/Dashboard'
import Customers from '../pages/Customers'
import Products from '../pages/Products'
import Form from '../pages/Form'
import Cars from '../pages/Cars'
import Login from '../pages/Login'
import Logout from '../pages/Logout'
import AddCar from '../pages/AddCar'
import ProviderDetails from '../pages/ProviderDetails'
import ChargingPortsPage from '../pages/ChargingPortsPage'
import Addport from '../pages/AddPort'

const Routes = () => {
    return (
        <Switch>
            <Route path='/' exact component={Dashboard}/>
            <Route path='/Customers' component={Customers}/>
            <Route path='/Products' component={Products}/>
            <Route path='/Form' component={Form}/>
            <Route path='/Cars' component={Cars}/>
            <Route path='/add-car' component={AddCar}/>
            {/* <Route path='/Login' component={Login}/> */}
            <Route path='/Logout' component={Logout}/>
            <Route path='/provider-details/:id' component={ProviderDetails}/>
            <Route path='/charging-port' component={ChargingPortsPage}/>
            <Route path='/add-port' component={Addport}/>
        </Switch>
    )
}

export default Routes
