import axios from 'axios';
import React, { Suspense } from 'react'
import { useState } from 'react';
import { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

const Addport = () => {
    let history = useHistory();
    const [port, setPort] = useState([]);

    const [success, setSuccess] = useState('');
    const [failed, setFailed] = useState('');

    const [portName, setPortName] = useState();
    const [portDesc, setPortDesc] = useState('');
    const [portType, setPortType] = useState('');
    // const [portImage, setPortImage] = useState('port-image.jpg');

    const adminDetails = localStorage.getItem("adminDetails");

    const submitHandle = (e) => {
        e.preventDefault();
        console.log(e.target.value);
        // let formData = new FormData();
        // formData.append("portName", portName);
        // formData.append("portType", portType);
        // formData.append("portDesc", portDesc);
        // formData.append("portImage", portImage);
        axios
            .post("https://noun.achievextesting.one/api/v1/admin/ports", { portName, portDesc, portType }, {
                headers: {
                    "content-type": "application/json"
                }
            })
            .then((result) => {
                console.log('tokenn');
                console.log(result);
                if (result.data.status === "success") {
                    console.log(`car add success`);
                    setSuccess(`Inserted Successful`);
                    history.push("/charging-port");
                } else {
                    console.log(`car add failed`);
                    setFailed(`Failed to Inserted`);
                }
            })
    }

    useEffect(() => {
        const item = localStorage.getItem("adminDetails");
        if (item) {
        } else {
            history.push("/Login");
        }
    }, [])

    return (
        <>
            <section>
                <div className='container-fluid'>
                    <div className='row'>
                        <div className='col-12'>
                            <h2>Add Port</h2>
                            <span style={{ "color": "green" }}>{success}</span>
                            <span>{failed}</span>
                            <form id="survey-form">
                                <div className="form-group">
                                    <label id="email-label" htmlFor="email">
                                        Port Name
                                    </label>
                                    <input
                                        type="text"
                                        name="portName"
                                        onChange={(e) => setPortName(e.target.value)}
                                        value={portName}
                                        className="form-control"
                                        placeholder="Enter brand name"
                                        required=""
                                    />
                                </div>
                                <div className="form-group">
                                    <label id="email-label" htmlFor="email">
                                        Port Type
                                    </label>
                                    <select name="portType" className='form-control' onChange={(e) => setPortType(e.target.value)}>
                                        <option value="">Select One</option>
                                        <option value="AC">AC</option>
                                        <option value="DC">DC</option>
                                    </select>
                                </div>
                                <div className="form-group">
                                    <label id="email-label" htmlFor="email">
                                        Port Description
                                    </label>
                                    <input
                                        type="text"
                                        name="portDesc"
                                        onChange={(e) => setPortDesc(e.target.value)}
                                        value={portDesc}
                                        className="form-control"
                                        placeholder="Enter sub-model"
                                        required=""
                                    />
                                </div>
                                {/* <div className="form-group">
                                    <label id="email-label" htmlFor="email">
                                        Port Image
                                    </label>
                                    <input
                                        type="file"
                                        name="portImage"
                                        onChange={(e) => setPortImage(e.target.files[0])}
                                        // value={carImage}
                                        className="form-control"
                                    />
                                </div> */}
                                <div className="form-group">
                                    <button type="submit" id="submit" onClick={submitHandle} className="btn btn-success w-50">
                                        Submit
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Addport