//     setFetchData(result.data);
// }
const fetcher = async (...args) => {
    const res = await fetch(...args);
    const data = await res.json();
    return data;
}
const Logout = () => {
    let history = useHistory();

    // useEffect(() => {
    //     const item = localStorage.getItem("adminDetails");
    //     if (item) {
    //     } else {
    //         window.location.reload(true);
    //     }
    // }, [])
    const logout = () => {
        localStorage.clear();
        window.location.reload(history.push("/Login"));
    }

    return (
        <>
            <div className="card">
                <div className="card-body">
                    <h5 className="card-title">Are you sure to logout?</h5>
                    <a className="btn btn-primary px-5" onClick={logout}>
                        Ok
                    </a>
                </div>
            </div>
        </>
    )
}

export default Logout 