import React from 'react'
import Skeleton from 'react-loading-skeleton'
import 'react-loading-skeleton/dist/skeleton.css'

export default function PortSkeleton({ counts }) {
    return Array(counts)
        .fill(0)
        .map((item, i) => (
            <>
                <tr key={i}>
                    <td><Skeleton /></td>
                    <td><Skeleton /></td>
                    <td><Skeleton /></td>
                    <td><Skeleton /></td>
                </tr>
            </>
        ))
}
