import React, { Suspense } from 'react'
import { useParams } from 'react-router-dom'
import ProviderDetailsComp from '../components/ProviderDetailsComp'

export default function ProviderDetails() {
    return (
        <>
            <div className="card">
                <div className="card-body">
                    <h4 className="card-title">Services provider details</h4>
                    {/* <p className="card-text">Some example text. Some example text.</p> */}
                    <Suspense fallback={<h3 style={{ "color": "green" }}>Loading...</h3>}>
                        <ProviderDetailsComp />
                    </Suspense>
                </div>
            </div>

        </>
    )
}
