import React, { useState } from 'react'
import { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import axios from 'axios'


const Login = () => {
    let history = useHistory();

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const [success, setSuccess] = useState("");
    const [error, setError] = useState("");

    useEffect(() => {
        const item = localStorage.getItem("adminDetails");
        if (item) {
            // history.push("/Cars");
            // const timer = setTimeout(() => {
            window.location.reload(true);
            // }, 1000);
        }
    }, [])

    const handleLogin = (e) => {
        e.preventDefault();
        if (email === "" || password === "") {
            alert("All fields are required");
        } else {
            axios
                .post("https://noun.achievextesting.one/api/v1/admin/login", {
                    email: email,
                    password: password
                })
                .then((result) => {
                    console.log(result);
                    if (result.data.status === "success") {
                        localStorage.setItem("adminDetails", result.data.token);
                        setSuccess(`Login ${result.data.status}`);
                        setError('');
                        // history.push("/Cars");
                        console.log(`login success imran`);
                        setTimeout(() => {
                            // history.push("/Cars");
                            window.location.href = "/";
                        }, 1000);
                    } else {
                        setError(`Invalid Login Details! ${result.data.status}.`);
                        setSuccess('');
                    }
                })
        }
    }

    return (
        <>
            <section>
                <div className="login">
                    <div className="form">
                        <form className="login-form" onSubmit={handleLogin}>
                            <div className='d-flex justify-content-center'>
                                <span className="bx bx-lock-alt"></span>
                                <p>LOGIN</p>
                            </div>
                            <small className='text-success'>{success}</small>
                            <small className='text-danger'>{error}</small>
                            <input type="text"
                                placeholder="Email"
                                onChange={(e) => setEmail(e.target.value)}
                                value={email} />

                            <input type="password"
                                placeholder="Password"
                                onChange={(e) => setPassword(e.target.value)}
                                value={password} />
                            <button>Login</button>
                        </form>
                    </div>
                </div>
            </section>

        </>
    )
}

export default Login 