
import axios from 'axios';
import React from 'react'
import { useState } from 'react';
import { Link, NavLink, useParams } from 'react-router-dom';
import useSWR from "swr";
import ProviderDetails from '../pages/ProviderDetails';


const fetcher = async (...args) => {
    const res = await fetch(...args);
    const data = await res.json();
    return data;
}
export default function ProviderDetailsComp() {
    let params = useParams();
    let id = params.id;

    const [approvalStatus, setApprovalStatus] = useState('');
    const [success, setSuccess] = useState('');
    const [failed, setFailed] = useState('');


    const handleApprovalStatus = (e) => {
        e.preventDefault();
        let status = approvalStatus;
        axios
            .put(`https://noun.achievextesting.one/api/v1/admin/approval-status/${id}`, {status}, {
                headers: {
                    // "Authorization": `Bearer ${adminDetails}`,
                    "access-control-allow-origin": "*"
                }
            })
            .then((result) => {
                console.log('tokenn');
                console.log(result);
                if (result.data.status === "success") {
                    console.log(`car add success`);
                    setSuccess(`Updated Successful! ${approvalStatus}`);
                } else {
                    console.log(`car add failed`);
                    setFailed(`Failed to Inserted`);
                }
            })
    }

    // const { data } = useSWR("https://noun.achievextesting.one/api/v1/admin/cars", fetcher, {
    const { data } = useSWR(`https://noun.achievextesting.one/api/v1/admin/provider/${id}`, fetcher, {
        suspense: true
    });
    console.log(`hello july`)
    console.log(data['data'])
    let allCars = data['data'];
    return (
        <>
            {
                allCars.map(function (item, i) {
                    console.log(item);
                    return <table className='table table-bordered'>
                        <tr>
                            <th>Name: </th>
                            <td>{item.name}</td>
                        </tr>
                        <tr>
                            <th>Email: </th>
                            <td>{item.email}</td>
                        </tr>
                        <tr>
                            <th>Phone: </th>
                            <td>{item.phone}</td>
                        </tr>
                        <tr>
                            <th>Pan Number: </th>
                            <td>{item.panNumber}</td>
                        </tr>
                        <tr>
                            <th>Pan Doc: </th>
                            <td>{item.panDoc}</td>
                        </tr>
                        <tr>
                            <th>GST number: </th>
                            <td>{item.gstNumber}</td>
                        </tr>
                        <tr>
                            <th>GST Certificate Doc: </th>
                            <td>{item.gstCertificateDoc}</td>
                        </tr>
                        <tr>
                            <th>Aadhar Number: </th>
                            <td>{item.aadharNumber}</td>
                        </tr>
                        <tr>
                            <th>Aadhar Front Doc: </th>
                            <td>{item.aadharFrontDoc}</td>
                        </tr>
                        <tr>
                            <th>Aadhar Back Doc: </th>
                            <td>{item.aadharBackDoc}</td>
                        </tr>
                        <tr>
                            <th>Station License Number: </th>
                            <td>{item.stationLicenseNumber}</td>
                        </tr>
                        <tr>
                            <th>Station License Doc: </th>
                            <td>{item.stationLicenseDoc}</td>
                        </tr>
                        <tr>
                            <th>Bank Name: </th>
                            <td>{item.bankName}</td>
                        </tr>
                        <tr>
                            <th>Bank Account No: </th>
                            <td>{item.bankAccountNo}</td>
                        </tr>
                        <tr>
                            <th>Bank IFSC Code: </th>
                            <td>{item.bankIfscCode}</td>
                        </tr>
                        <tr>
                            <th>Bank UPI: </th>
                            <td>{item.bankUpi}</td>
                        </tr>
                        <tr>
                            <th>Approval Status: </th>
                            <td>
                                <form >
                                    <div className="row">
                                        <span className='text-success b'>{failed}</span>
                                        <span className='text-success b'>{success}</span>
                                        <input type="hidden" value={item.providerKey} />
                                        <div className="col-6">
                                            <select className='form-control w-100' onChange={(e) => setApprovalStatus(e.target.value)} >
                                                <option value="">Change One</option>
                                                <option value="inreview" selected={item.approvalStatus==="inreview" ? true : false}>Inreview</option>
                                                <option value="approved" selected={item.approvalStatus==="approved" ? true : false}>Approved</option>
                                                <option value="rejected" selected={item.approvalStatus==="rejected" ? true : false}>Rejected</option>
                                                <option value="inactive" selected={item.approvalStatus==="inactive" ? true : false}>Inactive</option>
                                            </select>
                                        </div>
                                        <div className="col-6">
                                            <input type="submit" className='btn btn-outline-success' onClick={handleApprovalStatus} value="Change" />
                                        </div>
                                    </div>
                                </form>
                            </td>
                        </tr>
                        <tr>
                            <th>User Status: </th>
                            <td>{item.userStatus}</td>
                        </tr>
                    </table>
                })
            }
        </>
    )
}
