
import React from 'react'
import useSWR from "swr";

const fetcher = async (...args) => {
    const res = await fetch(...args);
    const data = await res.json();
    return data;
}
export default function CustomerList() {
    const { data } = useSWR("https://noun.achievextesting.one/api/v1/admin/customer-list", fetcher, {
        suspense: true
    });
    console.log(`hello july`)
    console.log(data['data'])
    let allCars = data['data'];
    return (
        <>
            {
                allCars.map(function (item, i) {
                    console.log(item);
                    return <tr key={i}>
                        <td>{i + 1}</td>
                        <td>{item.name}</td>
                        <td>{item.email}</td>
                        <td>{item.phone}</td>
                        <td className='fw-bolder text-success'>{item.status}</td>
                    </tr>
                })
            }
        </>
    )
}
