import React from 'react'
import useSWR from "swr";

const fetcher = async (...args) => {
    const res = await fetch(...args);
    const data = await res.json();
    return data;
}
export default function ChargingPortsDetails() {

    const { data } = useSWR("https://noun.achievextesting.one/api/v1/common/charging-ports", fetcher, {
        suspense: true
    });
    console.log(`hello charging ports`)
    console.log(data.data)
    let allPorts = data.data;
    return (
        <>
            {
                allPorts.map(function (item, i) {
                    console.log(item);
                    return <tr key={i}>
                        <td>{i + 1}</td>
                        <td>{item.portName}</td>
                        <td>{item.portType}</td>
                        <td className='fw-bolder text-success'>{item.status}</td>
                    </tr>
                })
            }
        </>
    )
}
