
import React from 'react'
import { Link, NavLink } from 'react-router-dom';
import useSWR from "swr";
import ProviderDetails from '../pages/ProviderDetails';
import { GrView } from 'react-icons/gr';

const fetcher = async (...args) => {
    const res = await fetch(...args);
    const data = await res.json();
    return data;
}
export default function ProviderList() {
    // const { data } = useSWR("https://noun.achievextesting.one/api/v1/admin/cars", fetcher, {
    const { data } = useSWR("https://noun.achievextesting.one/api/v1/admin/providers", fetcher, {
        suspense: true
    });
    console.log(`hello july`)
    console.log(data['data'])
    let allCars = data['data'];
    return (
        <>
            {
                allCars.map(function (item, i) {
                    console.log(item);
                    return <tr key={i}>
                        <td>{i + 1}</td>
                        <td>{item.name}</td>
                        <td>{item.email}</td>
                        <td>{item.phone}</td>
                        <td className='text-uppercase fw-bolder alert'>{item.approvalStatus}</td>
                        <td>{item.userStatus}</td>
                        <td><NavLink className="btn fw-bolder" to={`provider-details/${item.id}`}><GrView /></NavLink></td>
                    </tr>
                })
            }
        </>
    )
}
