import React, { Suspense } from 'react'
import { useState } from 'react';
import { useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import ChargingPortsDetails from '../components/ChargingPortDetails';
import ChargingPorts from '../components/ChargingPorts';
import PortSkeleton from '../components/PortSkeleton';
import TableSkeleton from '../components/TableSkeleton';

const ChargingPortsPage = () => {
    let history = useHistory();
    const [car, setCar] = useState([]);

    useEffect(() => {
        const item = localStorage.getItem("adminDetails");
        if (item) {
        } else {
            history.push("/Login");
        }
    }, [])

    return (
        <div>
            <h2 className="page-header">
                Ports
                <Link to={"/add-port"} className='btn btn-success'> +Add</Link>
            </h2>
            <div className='row'>
                <table>
                    <tr>
                        <th>SL.No.</th>
                        <th>Port Name</th>
                        <th>Port Type</th>
                        <th>Status</th>
                    </tr>
                    <Suspense fallback={<PortSkeleton counts={10} />}>
                        <ChargingPortsDetails />
                    </Suspense>
                </table>
            </div>
        </div>
    )
}

export default ChargingPortsPage