import axios from 'axios';
import React, { Suspense } from 'react'
import { useState } from 'react';
import { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import ChargingPorts from '../components/ChargingPorts';

const AddCar = () => {
    let history = useHistory();
    const [port, setPort] = useState([]);

    const [success, setSuccess] = useState('');
    const [failed, setFailed] = useState('');

    const [chargingPortKey, setChargingPortKey] = useState();
    const [carBrand, setCarBrand] = useState('');
    const [carModel, setCarModel] = useState('');
    const [carSubModel, setCarSubModel] = useState('');
    const [carImage, setCarImage] = useState('car-image.jpg');
    // let carImage = "no.jpg";
    const adminDetails = localStorage.getItem("adminDetails");

    const submitHandle = (e) => {
        e.preventDefault();
        console.log(e.target.value);
        let formData = new FormData();
        formData.append("chargingPortKey", chargingPortKey);
        formData.append("carBrand", carBrand);
        formData.append("carModel", carModel);
        formData.append("carSubModel", carSubModel);
        formData.append('carImage', carImage);
        axios
            .post("https://noun.achievextesting.one/api/v1/admin/cars", formData, {
                headers: {
                    "Authorization": `Bearer ${adminDetails}`,
                    "access-control-allow-origin": "*"
                }
            })
            .then((result) => {
                console.log('tokenn');
                console.log(result);
                if (result.data.status === "success") {
                    console.log(`car add success`);
                    setSuccess(`Inserted Successful`);
                    history.push("/Cars");
                } else {
                    console.log(`car add failed`);
                    setFailed(`Failed to Inserted`);
                }
            })
    }

    useEffect(() => {
        const item = localStorage.getItem("adminDetails");
        if (item) {
        } else {
            history.push("/Login");
        }
    }, [])

    return (
        <>
            <section>
                <div className='container-fluid'>
                    <div className='row'>
                        <div className='col-12'>
                            <h2>Add Car</h2>
                            <span style={{ "color": "green" }}>{success}</span>
                            <span>{failed}</span>
                            <form id="survey-form">
                                <div className="form-group">
                                    <p>Charging Port</p>
                                    <Suspense fallback={<span style={{ "color": "green", "border": "1px solid green", "padding": "10px" }}>Loading...</span>}>
                                        <select id="dropdown" name="chargingPortKey" onChange={(e) => setChargingPortKey(e.target.value)} className="form-control" required="">
                                            <option disabled="" selected=""> Select One </option>
                                            <ChargingPorts />
                                        </select>
                                    </Suspense>
                                </div>
                                <div className="form-group">
                                    <label id="email-label" htmlFor="email">
                                        Car Brand
                                    </label>
                                    <input
                                        type="text"
                                        name="carBrand"
                                        id="carBrand"
                                        onChange={(e) => setCarBrand(e.target.value)}
                                        value={carBrand}
                                        className="form-control"
                                        placeholder="Enter brand name"
                                        required=""
                                    />
                                </div>
                                <div className="form-group">
                                    <label id="email-label" htmlFor="email">
                                        Car Model
                                    </label>
                                    <input
                                        type="text"
                                        name="carModel"
                                        id="carModel"
                                        onChange={(e) => setCarModel(e.target.value)}
                                        value={carModel}
                                        className="form-control"
                                        placeholder="Enter model name"
                                        required=""
                                    />
                                </div>
                                <div className="form-group">
                                    <label id="email-label" htmlFor="email">
                                        Car Sub-model
                                    </label>
                                    <input
                                        type="text"
                                        name="carSubModel"
                                        id="carSubModel"
                                        onChange={(e) => setCarSubModel(e.target.value)}
                                        value={carSubModel}
                                        className="form-control"
                                        placeholder="Enter sub-model"
                                        required=""
                                    />
                                </div>
                                <div className="form-group">
                                    <label id="email-label" htmlFor="email">
                                        Car Image
                                    </label>
                                    <input
                                        type="file"
                                        name="carImage"
                                        id="carImage"
                                        onChange={(e) => setCarImage(e.target.files[0])}
                                        // value={carImage}
                                        className="form-control"
                                        placeholder="Enter image"
                                    />
                                </div>
                                <div className="form-group">
                                    <button type="submit" id="submit" onClick={submitHandle} className="btn btn-success w-50">
                                        Submit
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default AddCar